@import './var.scss';
html {
  height: 100%;
  -webkit-overflow-scrolling : touch; 
}
body {
    width: 100%;
    max-width: 750px;
    height: 100%;
    margin: 0 auto;
    font: 14px/1.4286 "微软雅黑", "STHeitiSC-Light", "Helvetice-Light", arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    color: #464646;
}
img{
  display: inline-block;
  vertical-align: middle;
}
input,select,button{
    font: inherit;
}
input[type='text']{
  -webkit-appearance: none;
}
.clearfix:after{
    content:'';
    display: table;
    clear: both;
}
a{
    color: green;
    text-decoration: none;
    -webkit-tap-highlight-color: transparent;
    &:active{
      opacity: 0.7;
    }
}
/* 1.去除android a/button/input标签被点击时产生的边框 2.去除ios a标签被点击时产生的半透明灰色背景 */
a,button,input{
    -webkit-tap-highlight-color:rgba(255,0,0,0);
}
ul{
    list-style: none;
    padding:0;
    margin:0;
}
h1,h2,h3,h4,p{
    margin:0;
}


// 移动端1像素
@media (-webkit-min-device-pixel-ratio: 1.5),(min-devicp-pixel-ratio: 1.5) {
    .border-top-1px{
        &:after{
            -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7)
        }
    }
    .border-bottom-1px{
        &:after{
            -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7)
        }
    }
}
@media (-webkit-min-device-pixel-ratio: 2),(min-devicp-pixel-ratio: 2) {
   border-top-1px{
        &:after{
            -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5)
        }
    }
    .border-bottom-1px{
        &:after{
            -webkit-transform: scaleY(0.5);
            transform: scaleY(0.5)
        }
    }
}




