$txtcolor: #464646;
$iconcolor:#969696;
$bordercolor:#d4d4d4;
$pagebg:#f1f1f1;
$barbg:#f7f7f7;
$bluecolor:#1c91d4;
$whitecolor:#fff;
$errorcolor:#d55533;
$font10:10px;
$font12:12px;
$font14:14px;
$font16:16px;
$font18:18px;
/* 常用css代码片段 */
.pb60{
	padding-bottom: 60px;
}
.fontBlue{
	color: $bluecolor;
}
.fontBold{
	font-weight: bold;
}

/* 文字超出一行隐藏显示... */
%textHidden1{
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

/* 数字英文自动换行 */
%textLineFeed {
	word-wrap: break-word;
	word-break: break-all;
}
 



